@import url(https://fonts.googleapis.com/css2?family=Akaya+Telivigala&display=swap);
/** @format */
.App {
  font-family: "Akaya Telivigala", cursive;
  text-align: center;
  margin: auto;
  background-color: #99e6ff;
}
.glass {
  border: solid 5px blueviolet;
  border-radius: 20%;
  width: 70%;
  margin: auto;
  padding: 30px;
  background-color: #b380ff;
}
.small {
  border: solid 3px #4dffff;
  background-color: #3dc8ff;
  background-image: url("https://www.transparenttextures.com/patterns/nice-snow.png");
  border-radius: 50%;
  width: 70px;
  height: 70px;
  margin: 10px;
}
.larg {
  border: solid 3px #4dffff;
  background-color: #0081bd;
  background-image: url("https://www.transparenttextures.com/patterns/asfalt-light.png");
  border-radius: 50%;
  width: 300px;
  height: 300px;
  margin: 10px;
}
img {
  height: 100px;
  width: 100px;
}
.sounds-div {
  border: solid 3px purple;
  border-radius: 10%;
  background-color: yellow;
}
.small-up-div {
  padding: 3vw;
  display: grid;
  grid-template-columns: auto auto auto auto auto auto;
}
h1 {
  font-size: 20vh;
  color: #6600ff;
}

/** @format */

.animal {
  border-radius: 50%;
  padding: 10px;
  margin: 15px;
  background-color: #ffcc00;
}
.animals-div {
  border: solid 3px orangered;
  border-radius: 20%;
  background-color: #009900;
}

/** @format */

.white {
  background-color: whitesmoke;
  height: 60vh;
  width: 5vw;
}
.black {
  background-color: #333;
  height: 40vh;
  width: 5vw;
  position: relative;
  bottom: 10vh;
  color: whitesmoke;
}
.piano {
  margin: 15px;
  padding: 30px;
  border: solid 5px;
  border-radius: 15%;
  background-color: whitesmoke;
}

/** @format */

.songs-btn {
  border-radius: 50%;
  padding: 10px;
  margin: 15px;
  background-color: #ff66cc;
  font-family: "Akaya Telivigala", cursive;
}

/** @format */
.vehicles-div {
  border: solid 3px turquoise;
  border-radius: 30%;
  background-color: #3399ff;
  color: #ccffff;
}
.vehicles-btn {
  border-radius: 50%;
  padding: 10px;
  margin: 15px;
  background-color: #006666;
}

